an dos razas la primera de ellas Landrace la cual tiene una gran adaptación a la diversidad de climas y la Yorkshire que posee una gran capacidad reproductora.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/img-3.png"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row",attrs:{"numero":"3","titulo":"Aves"}},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',{staticClass:"text-small"},[_vm._v("Generalmente gallinas y pollos, es tal vez el sistema de producción pecuario más común y adoptado por familias en menor escala debido a que su producción es menos compleja y de menor tiempo en comparación con el resto de las especies. Su aporte de carne con grandes contenidos nutricionales y del huevo rico en proteína como un alimento base de las comidas colombianas. Esta actividad también es realizada a gran escala donde la elección de la raza es determinante para el tipo de producción que se desee, ya sea pollos de engorde o gallinas ponedoras que a diferencia de las criollas tienen tiempos de crecimiento y producción mucho menores favoreciendo los intereses del productor.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/img-4.png"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row",attrs:{"numero":"4","titulo":"Caprinos y ovinos"}},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',{staticClass:"text-small"},[_vm._v("También fueron introducidos después de la conquista española principalmente como abastecimiento de lana, carne y leche. Existen dos tipos de ovinos (ovejas) los de lana y los de pelo, siendo estos últimos los más adaptables a los climas tropicales, sin embargo, debido a la gran biodiversidad de climas en Colombia la oveja de lana también es una opción en los lugares que sean aptos para su producción. Los caprinos y los ovinos son considerados como pequeños rumiantes y tienen la ca